.mask {
    width: 100%;
    height: 100vh;
    position: relative;
    overflow: hidden; /* Prevent scroll bars */
}

.homebackground-img {
    width: 100%;
    height: 100%;
    max-width: 100%;
    max-height: 100%;
    object-fit: cover; /* Ensure the image covers the container */
}

.mask::after {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.5); /* Add a black overlay */
    opacity: 0.8;
}

.home {
    height: 100%;
    width: 100%;
}

.home .content {
    position: absolute;
    transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    text-align: center;
}

.home .content h1 {
    font-size: 4rem;
    padding: 0.6rem 0 1.5rem;
}

.home .content p {
    font-size: 1.4rem;
    font-weight: 200;
    text-transform: uppercase;
}

.content .btn {
    margin: 1rem 0.2rem;
}

@media screen and (max-width: 640px) {
    .home .content h1 {
        font-size: 3rem;
    }

    .home .content p {
        font-size: 1.4rem;
    }
}