.about-img {
    width: 100%;
    background: rgba(0, 0, 0, 0.5);
    height: 60vh;
    position: relative;
}

.about-img::before {
    content: "";
    background: url(https://plus.unsplash.com/premium_photo-1685086785636-2a1a0e5b591f?q=80&w=1032&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D);
    background-size: cover;
    background-position: center top;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
}   

.heading {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.about-img h1 {
    font-size: 2.4rem;
}

.about-img p {
    font-size: 1.4rem;
    text-align: center;
}

@media screen and (min-width: 640px) {
    .about-img h1 {
        font-size: 2rem;
    }
}
